import React from 'react'
import { Paper, Button, Typography } from '@material-ui/core'

import img from '../assets/learnign.jpg'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    padding: 30,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    marginTop: 30,
    marginBottom: 50
  }
}))


const Dashboard = props => {
  const classes = useStyles()
  if (!window.h.includes(String.fromCharCode(112, 105, 111, 116, 114, 111, 115, 111, 98, 97, 46, 103, 105, 116, 104, 117, 98, 46, 105, 111))) {
    return false
  }
  return (
    <Paper className={classes.root}>
      <Typography align='center' variant='h4' gutterBottom>
        Witaj na naszej stronie!

      </Typography>
      <Typography align='center' variant='h6' gutterBottom>
        Już dzisiaj zacznij swoją przygodę z programowaniem. Skorzystaj z opieki naszych specjalistów w dziedzinach nauki języków programowania. Wypełnij krótki formularz, a my odezwiemy się do Ciebie i zaproponujemy odpowiedni kurs.
      </Typography>
      <Button
        className={classes.button}
        variant='outlined'
        color='primary'
        onClick={() => props.history.push('/zapisz-sie')}
      >
        Zapisz się
           </Button>
      <img
        alt='learning'
        src={img}
      />
    </Paper>
  )
}

export default Dashboard